import axios from 'axios'
import React, { useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { onApi } from '../../Functions/Func1'
import { get_requests } from '../../Store/store'
import Request from './Request'

const Attention = () => {
  const dispatch = useDispatch()
  const requests = useSelector(state=>state.admin.requests)

  useEffect(()=>{
    let fetch = axios.get(onApi+'/fetchrequests')
    fetch.then(res=>{
      if(res.err){
        return
      }
      dispatch(get_requests(res.data.requests.filter(f=>f.customerSorted === false)))
    })
  })
  
  return (
    <div className='requestpage'>
      <nav>
        <h1>
          Customer Requests
        </h1></nav>
      <div>
        {requests.map(req => <Request 
          key={req._id}
          request={req}
        />)}
      </div>
      
    </div>
  )
}

export default Attention
