import axios from 'axios'
import React from 'react'
import { onApi } from '../../Functions/Func1'
import './admComp.css'

const Request = (props) => {
    let req = props.request

    const answer = async() =>{
        await axios.post(onApi+'/answerrequest', 
        {id: req._id})
        .then(res=>{

        })
    }

  return (
    <div className='request'>
      <h2>Table Number: {req.tableNumber}</h2>
      <>
        <p>
            {req.message ? req.message : "no message"}
        </p>
      </>
      
      <button className='simpleblack'
        onClick={answer}
      >
        okay
      </button>
    </div>
  )
}

export default Request
